<template>
  <main class="m-24px flex-col gap-8px mb-64px">
    <v-tabs v-model="selectedTab" align-tabs="center" color="deep-purple-accent-4">
      <v-tab v-for="({ id, title }, index) of tabs" :key="index" :value="id">
        {{ title }}
        <p
          v-if="id === selectedTab"
          class="w-24px h-24px flex-center"
          @click="showEditTabDialog = true"
        >
          <i class="fa-solid fa-pen ml-4px" />
        </p>
      </v-tab>
      <v-tab @click="addTab">
        <i class="fas fa-plus text-16px" />
      </v-tab>
    </v-tabs>
    <div class="flex-center w-full gap-4px">
      <div class="flex-1">
        <v-text-field
          :model-value="currentAmount"
          :label="`剩餘${foreignCurrencyString}`"
          hide-details="auto"
          inputmode="decimal"
          @update:model-value="
            (newValue: string) => update('amount', checkNumberAndReturn(newValue))
          "
        />
      </div>
      <div class="flex-1">
        <v-text-field :model-value="averageRate" hide-details="auto" label="平均匯率" disabled />
      </div>
    </div>
    <v-data-table :headers="headers" :items="rows">
      <!-- eslint-disable-next-line vue/valid-v-slot -->
      <template #item.actions="{ item }">
        <i class="fa-solid fa-trash" @click="deleteItemEvent(item)" />
      </template>
    </v-data-table>
    <div class="fixed bottom-24px right-24px" @click="showAddItemDialog = true">
      <v-btn icon="mdi-plus" />
    </div>
  </main>
  <v-dialog v-model="showAddItemDialog">
    <v-card title="Add Item">
      <template #text>
        <v-text-field
          v-model="addItem.date"
          type="datetime-local"
          hide-details="auto"
          label="交易日期"
        />
        <v-text-field
          :model-value="addItem.sell"
          hide-details="auto"
          inputmode="decimal"
          :label="`賣出${localCurrencyString}`"
          @update:model-value="
            (newValue: string) => (addItem.sell = checkNumberAndReturn(newValue))
          "
        />
        <v-text-field
          :model-value="addItem.buy"
          hide-details="auto"
          inputmode="decimal"
          :label="`買入${foreignCurrencyString}`"
          @update:model-value="(newValue: string) => (addItem.buy = checkNumberAndReturn(newValue))"
        />
        <v-text-field
          hide-details="auto"
          label="平均匯率"
          :model-value="addItem.buy === 0 ? 0 : round(addItem.sell / addItem.buy)"
          disabled
        />
      </template>
      <template #actions>
        <v-btn class="ms-auto" text="Submit" @click="addItemEvent" />
      </template>
    </v-card>
  </v-dialog>
  <v-dialog v-model="showEditTabDialog">
    <v-card v-if="editTab !== null" title="Setting">
      <template #text>
        <div class="flex-col gap-8px">
          <v-text-field v-model="editTab.title" hide-details="auto" label="標籤名稱" />
          <v-text-field
            v-model="editTab.locale"
            hide-details="auto"
            label="語系代碼"
            placeholder="zh-TW"
          />
          <v-text-field
            v-model="editTab.localCurrencyCode"
            hide-details="auto"
            label="本幣代碼"
            placeholder="TWD"
          />
          <v-text-field
            v-model="editTab.foreignCurrencyCode"
            hide-details="auto"
            label="外幣代碼"
            placeholder="JPY"
          />
          <v-textarea
            v-model="editTab.config"
            :error-messages="configIsValid ? '' : 'json 無效'"
            label="設定檔"
            messages="若有修改到設定檔上方其他設定就會忽略"
          />
        </div>
      </template>
      <template #actions>
        <div class="flex items-center justify-between w-full">
          <v-btn text="Delete" color="red" @click="deleteTabEvent(selectedTab)" />
          <div>
            <v-btn text="Cancel" @click="showEditTabDialog = false" />
            <v-btn text="Save" color="blue" @click="saveTabEvent" />
          </div>
        </div>
      </template>
    </v-card>
  </v-dialog>
  <v-dialog :model-value="Boolean(deleteData)" @update:model-value="deleteData = null">
    <v-card class="mx-auto" max-width="320" title="Update Field">
      <template #text> {{ deleteData?.message ?? '' }} </template>
      <template #actions>
        <v-btn class="ms-auto" text="Cancel" @click="deleteData = null" />
        <v-btn class="ms-auto" color="red" text="Delete" @click="deleteData?.event" />
      </template>
    </v-card>
  </v-dialog>
</template>
<script lang="ts" setup>
import { format } from 'date-fns'
import { computed, onMounted, ref, watch } from 'vue'

import type { UUID } from '@/types'
import { generateUuid, isTruthyString, roundByDigits } from '@/utils'
import { isUuid } from '@/utils/checkTyping'
import { convertField, convertToDate } from '@/utils/converts'
import { sortListByDate } from '@/utils/sorts'
import { localStorageManager } from '@/utils/StorageManager'

import {
  type AverageExchangeRateItem,
  type AverageExchangeRateData,
  type AverageExchangeRateGroup,
  initAverageExchangeRateData,
  formatCurrency,
  checkFormatCurrency,
  isAverageExchangeRateData,
} from '.'

interface DataRow {
  id: UUID
  date: string
  sell: number
  buy: number
  exchangeRate: number
  balance: number
}
interface TableRow {
  id: UUID
  date: string
  sell: string
  buy: string
  exchangeRate: number
  balance: string
}

const round = roundByDigits(4)

const headers = computed(
  () =>
    [
      { title: '日期', key: 'date', nowrap: true, sortable: false },
      {
        title: `賣出${localCurrencyString.value}`,
        key: 'sell',
        nowrap: true,
        sortable: false,
        align: 'end',
      },
      {
        title: `買入${foreignCurrencyString.value}`,
        key: 'buy',
        nowrap: true,
        sortable: false,
        align: 'end',
      },
      { title: '匯率', key: 'exchangeRate', nowrap: true, sortable: false },
      {
        title: `剩餘${foreignCurrencyString.value}`,
        key: 'balance',
        nowrap: true,
        sortable: false,
        align: 'end',
      },
      {
        key: 'actions',
        sortable: false,
      },
    ] as const,
)

const rows = computed((): TableRow[] => {
  return dataRows.value.map((item) => ({
    ...item,
    sell: formatCurrency(
      item.sell,
      currentData.value?.locale,
      currentData.value?.localCurrencyCode,
    ),
    buy: formatCurrency(
      item.buy,
      currentData.value?.locale,
      currentData.value?.foreignCurrencyCode,
    ),
    balance: formatCurrency(
      item.balance,
      currentData.value?.locale,
      currentData.value?.foreignCurrencyCode,
    ),
  }))
})
const dataRows = computed((): DataRow[] => {
  let totalAmount = currentAmount.value
  return sortListByDate(convertField(currentList.value, 'date', convertToDate), 'date', 'desc').map(
    (item) => {
      const balance = totalAmount > item.buy ? item.buy : totalAmount
      totalAmount -= balance
      return {
        id: item.id,
        date: format(item.date, 'yyyy/MM/dd'),
        sell: item.sell,
        buy: item.buy,
        exchangeRate: round(item.sell / item.buy),
        balance,
      }
    },
  )
})
const averageRate = computed((): number => {
  let sellTotal = 0
  let buyTotal = 0
  for (const item of dataRows.value) {
    if (item.balance > 0) {
      buyTotal += item.balance
      sellTotal += item.balance * item.exchangeRate
    }
  }
  return buyTotal === 0 ? 0 : round(sellTotal / buyTotal)
})

const showAddItemDialog = ref(false)
const showEditTabDialog = ref(false)
const selectedTab = ref<UUID | null>(null)
const restoreData = ref<AverageExchangeRateGroup>({})
const currentData = computed((): AverageExchangeRateData | null =>
  selectedTab.value !== null ? (restoreData.value[selectedTab.value] ?? null) : null,
)
const currentList = computed((): AverageExchangeRateItem[] => currentData.value?.list ?? [])
const currentAmount = computed((): number => currentData.value?.amount ?? 0)
const tabs = computed((): { id: string; title: string }[] => {
  return Object.entries(restoreData.value).map(([id, { title }]) => ({ id, title }))
})
const localCurrencyString = computed(() =>
  isTruthyString(currentData.value?.localCurrencyCode)
    ? ` ${currentData.value.localCurrencyCode}`
    : '本幣',
)
const foreignCurrencyString = computed(() =>
  isTruthyString(currentData.value?.foreignCurrencyCode)
    ? ` ${currentData.value.foreignCurrencyCode}`
    : '外幣',
)

onMounted(() => {
  restoreData.value = localStorageManager.get('averageExchangeRate') ?? {}
  checkAndResetTab()
})
function checkAndResetTab(): void {
  if (Object.keys(restoreData.value).length === 0) {
    addTab()
  }
  const tabId = Object.keys(restoreData.value)[0]
  if (isUuid(tabId)) {
    selectedTab.value = tabId
  }
}

watch(showAddItemDialog, () => {
  addItem.value = resetItem()
})
function resetItem(): Omit<AverageExchangeRateItem, 'id'> {
  return {
    sell: 0,
    buy: 0,
    date: format(new Date(), "yyyy-MM-dd'T'HH:mm"),
  }
}

const addItem = ref<Omit<AverageExchangeRateItem, 'id'>>(resetItem())
interface EditTab {
  title: string
  localCurrencyCode: string
  foreignCurrencyCode: string
  locale: string
  config: string
}
const editTab = ref<EditTab | null>(null)

function addItemEvent(): void {
  const { buy, sell } = addItem.value
  const date = new Date(addItem.value.date).toISOString()
  const id = generateUuid()
  update('list', [{ buy, sell, date, id }, ...currentList.value])
  showAddItemDialog.value = false
}

function addTab(): void {
  const id = generateUuid()
  restoreData.value[id] = initAverageExchangeRateData()
  selectedTab.value = id
}

watch(
  restoreData,
  () => {
    localStorageManager.set('averageExchangeRate', restoreData.value)
  },
  { deep: true },
)

function update<Key extends keyof AverageExchangeRateData>(
  key: Key,
  value: AverageExchangeRateData[Key],
): void {
  if (selectedTab.value === null) {
    return
  }
  const data: AverageExchangeRateData =
    restoreData.value[selectedTab.value] ?? initAverageExchangeRateData()
  data[key] = value
}

watch(showEditTabDialog, (show) => {
  if (!show) {
    editTab.value = null
    return
  } else if (currentData.value !== null) {
    editTab.value = {
      title: currentData.value.title,
      localCurrencyCode: currentData.value.localCurrencyCode ?? '',
      foreignCurrencyCode: currentData.value.foreignCurrencyCode ?? '',
      locale: currentData.value.locale ?? '',
      config: JSON.stringify(currentData.value, null, 2),
    }
  }
})

function saveTabEvent(): void {
  if (editTab.value === null) {
    return
  }
  const { title, localCurrencyCode, foreignCurrencyCode, locale, config } = editTab.value
  const configJson = checkAverageExchangeRateConfig(config)
  if (
    configJson !== null &&
    selectedTab.value !== null &&
    JSON.stringify(configJson) !== JSON.stringify(currentData.value)
  ) {
    restoreData.value[selectedTab.value] = configJson
  } else {
    checkFormatCurrency({ locale, localCurrencyCode, foreignCurrencyCode })
    update('title', title)
    update('localCurrencyCode', isTruthyString(localCurrencyCode) ? localCurrencyCode : null)
    update('foreignCurrencyCode', isTruthyString(foreignCurrencyCode) ? foreignCurrencyCode : null)
    update('locale', isTruthyString(locale) ? locale : null)
  }
  showEditTabDialog.value = false
}

const deleteData = ref<{ message: string; event: () => void } | null>(null)
function deleteItemEvent({ id }: TableRow): void {
  deleteData.value = {
    message: '確定要刪除這筆資料嗎？',
    event: (): void => {
      update(
        'list',
        [...currentList.value].filter((item) => item.id !== id),
      )
      deleteData.value = null
    },
  }
}
function deleteTabEvent(id: UUID | null): void {
  if (id === null) {
    return
  }
  deleteData.value = {
    message: '確定要刪除這張表嗎？',
    event: (): void => {
      // eslint-disable-next-line security/detect-object-injection, @typescript-eslint/no-dynamic-delete
      delete restoreData.value[id]
      checkAndResetTab()
      deleteData.value = null
      showEditTabDialog.value = false
    },
  }
}

function checkNumberAndReturn(newValue: string): number {
  const newNumber = parseFloat(newValue)
  return isNaN(newNumber) ? 0 : newNumber
}

const configIsValid = ref(true)
watch(
  () => editTab.value?.config,
  (data) => {
    configIsValid.value = Boolean(checkAverageExchangeRateConfig(data))
  },
)
function checkAverageExchangeRateConfig(data: string | undefined): AverageExchangeRateData | null {
  if (data === undefined) {
    return null
  }
  let jsonData: unknown
  try {
    jsonData = JSON.parse(data)
  } catch {
    return null
  }
  return isAverageExchangeRateData(jsonData) ? jsonData : null
}
</script>
